import Vue from 'vue'
import Router from 'vue-router'
import MtlHome from '@/pages/mtl-home'
import Address from '../pages/Address'
import Date from '../pages/mtl-date'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'MtlHome',
      component: MtlHome
    },
    {
      path:'/Address',
      name:'Address',
      component:Address
    },
    {
      path:'/date',
      name:'Date',
      component:Date
    }
  ]
})



